body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.main {
  --column-count: 4;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 0;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 300px) {
  .main {
    --column-count: 1;
  }
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  transition: transform .3s;
}

.card__image {
  box-sizing: border-box;
  background-repeat: no-repeat;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__description {
  color: #060b35;
  width: 100%;
  height: 36px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card:hover .card__description {
  color: #34568b;
}

.card__product-code {
  box-sizing: border-box;
  color: #616070;
  width: 98px;
  height: 14px;
  margin: 0 0 16px;
}

.card__grade {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin: 0;
  display: flex;
}

.card__review {
  color: #060b35;
  background-position: 50%;
  background-repeat: no-repeat;
  margin: 0;
  padding: 0;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  text-align: center;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__price-cost {
  text-align: center;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  justify-content: center;
  align-items: center;
  height: 16px;
  margin: 0;
  padding: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
